// src/lib/ebikes/deleteEbikesByCompany.ts

import { collection, deleteDoc, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

/**
 * X√≥a to√†n b·ªô ebikes thu·ªôc v·ªÅ m·ªôt c√¥ng ty c·ª• th·ªÉ.
 * @param companyId ID c·ªßa c√¥ng ty
 * @returns S·ªë l∆∞·ª£ng ebike ƒë√£ b·ªã x√≥a
 */
export async function deleteAllEbikesByCompany(companyId: string): Promise<number> {
  const q = query(collection(db, 'ebikes'), where('companyId', '==', companyId));
  const snapshot = await getDocs(q);

  const deletePromises = snapshot.docs.map((docSnap) => deleteDoc(docSnap.ref));
  await Promise.all(deletePromises);

  return snapshot.size;
}
