'use client';

import { useEffect, useState } from 'react';
import {
  collection,
  getDocs,
  query,
  where,
  GeoPoint,
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';

export interface RentalStation {
  id: string;
  name: string;
  address: string;
  location: GeoPoint;
  totalEbikes: number;
}

export interface RentalCompany {
  id: string;
  name: string;
  email: string;
  phone: string;
  address: string;
  stations: RentalStation[];
}

export const useEbikeData = (companyId?: string | null) => {
  const [rentalCompanies, setRentalCompanies] = useState<RentalCompany[]>([]);
  const [ebikeModels, setEbikeModels] = useState<EbikeModel[]>([]);
  const [ebikes, setEbikes] = useState<Ebike[]>([]);

  useEffect(() => {
    if (companyId) {
      fetchRentalCompanies(companyId);
      fetchEbikeModels(companyId);
      fetchEbikes(companyId);
    }
  }, [companyId]);

  const fetchRentalCompanies = async (companyId: string) => {
    const companyRef = query(
      collection(db, 'rentalCompanies'),
      where('ownerId', '==', companyId)
    );

    const snapshot = await getDocs(companyRef);
    const companies = await Promise.all(
      snapshot.docs.map(async (docSnap) => {
        const companyData = docSnap.data();
        const stationsSnapshot = await getDocs(
          query(collection(db, 'rentalStations'), where('companyId', '==', docSnap.id))
        );

        const stations: RentalStation[] = stationsSnapshot.docs.map((stationDoc) => ({
          id: stationDoc.id,
          name: stationDoc.data().name || '',
          address: stationDoc.data().displayAddress || '',
          location:
            stationDoc.data().location instanceof GeoPoint
              ? stationDoc.data().location
              : new GeoPoint(0, 0),
          totalEbikes: stationDoc.data().totalEbikes || 0,
        }));

        return {
          id: docSnap.id,
          name: companyData.name,
          email: companyData.email,
          phone: companyData.phone,
          address: companyData.displayAddress,
          stations,
        };
      })
    );

    setRentalCompanies(companies);
  };

  const fetchEbikeModels = async (companyId: string) => {
    const ref = query(
      collection(db, 'ebikeModels'),
      where('companyId', '==', companyId)
    );

    const snapshot = await getDocs(ref);
    const data = snapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    })) as EbikeModel[];

    setEbikeModels(data);
  };

  const fetchEbikes = async (companyId: string) => {
    const ref = query(
      collection(db, 'ebikes'),
      where('companyId', '==', companyId)
    );

    const snapshot = await getDocs(ref);
    const data = snapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    })) as Ebike[];

    setEbikes(data);
  };

  return {
    rentalCompanies,
    ebikeModels,
    ebikes,
    setEbikes,
    setEbikeModels,
    fetchRentalCompanies,
    fetchEbikeModels,
    fetchEbikes,
  };
};
