'use client';

import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select'; // ‚úÖ d√πng Select t·ª± custom
import { useEbikeForm } from '@/src/hooks/useEbikeForm';
import { EbikeStatus } from '@/src/lib/ebikes/ebikeTypes';

interface Props {
  companyId: string;
  onSaveComplete?: () => void;
}

const statusOptions: EbikeStatus[] = [
  'Available',
  'In Use',
  'Reserved',
  'Under Maintenance',
  'Broken',
];

export default function EbikeForm({ companyId, onSaveComplete }: Props) {
  const {
    ebike,
    isUpdateMode,
    handleChange,
    handleSave,
    loading,
  } = useEbikeForm(companyId, onSaveComplete);

  return (
    <div className="bg-white p-6 rounded-xl shadow mt-6">
      <h2 className="text-xl font-semibold mb-4">
        {isUpdateMode ? 'Edit Ebike' : 'Add New Ebike'}
      </h2>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Input
          placeholder="Serial Number"
          value={ebike.serialNumber}
          onChange={(e) => handleChange('serialNumber', e.target.value)}
        />
        <Input
          placeholder="Vehicle ID (VIN)"
          value={ebike.vehicleID}
          onChange={(e) => handleChange('vehicleID', e.target.value)}
        />
        <Input
          placeholder="Plate Number"
          value={ebike.plateNumber}
          onChange={(e) => handleChange('plateNumber', e.target.value)}
        />
        <Input
          type="number"
          placeholder="Odo (km)"
          value={ebike.odo}
          onChange={(e) => handleChange('odo', e.target.valueAsNumber || 0)}
        />
        <Input
          placeholder="Color"
          value={ebike.color}
          onChange={(e) => handleChange('color', e.target.value)}
        />
        <Textarea
          placeholder="Current Location"
          value={ebike.currentLocation}
          onChange={(e) => handleChange('currentLocation', e.target.value)}
        />
        <SimpleSelect
          options={statusOptions.map((s) => ({ label: s, value: s }))}
          placeholder="Select Status"
          value={ebike.status}
          onChange={(val) => handleChange('status', val as EbikeStatus)}
        />
        <Input
          type="number"
          placeholder="Battery Capacity (Wh)"
          value={ebike.batteryCapacity}
          onChange={(e) => handleChange('batteryCapacity', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Range (km)"
          value={ebike.range}
          onChange={(e) => handleChange('range', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Price/Hour"
          value={ebike.pricePerHour || ''}
          onChange={(e) => handleChange('pricePerHour', e.target.valueAsNumber || undefined)}
        />
        <Input
          type="number"
          placeholder="Price/Day"
          value={ebike.pricePerDay}
          onChange={(e) => handleChange('pricePerDay', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Price/Week"
          value={ebike.pricePerWeek || ''}
          onChange={(e) => handleChange('pricePerWeek', e.target.valueAsNumber || undefined)}
        />
        <Input
          type="number"
          placeholder="Price/Month"
          value={ebike.pricePerMonth || ''}
          onChange={(e) => handleChange('pricePerMonth', e.target.valueAsNumber || undefined)}
        />
      </div>

      <Button onClick={handleSave} className="mt-6" disabled={loading}>
        {loading ? 'Saving...' : isUpdateMode ? 'Save Changes' : 'Add Ebike'}
      </Button>
    </div>
  );
}
