// hooks/useEbikeForm.ts
import { useState } from 'react';
import {
  addDoc,
  collection,
  doc,
  getDocs,
  serverTimestamp,
  updateDoc,
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Ebike, EbikeStatus } from '@/src/lib/ebikes/ebikeTypes';

export function useEbikeForm(companyId: string, onSaveComplete?: () => void) {
  const [loading, setLoading] = useState(false);
  const [isUpdateMode, setIsUpdateMode] = useState(false);
  const [ebike, setEbike] = useState<Ebike>({
    id: '',
    companyId,
    modelId: '',
    stationId: '',
    serialNumber: '',
    vehicleID: '',
    plateNumber: '',
    odo: 0,
    color: '',
    status: 'Available',
    currentLocation: '',
    lastMaintained: null,
    batteryCapacity: 0,
    range: 0,
    pricePerDay: 0,
    pricePerHour: undefined,
    pricePerWeek: undefined,
    pricePerMonth: undefined,
  });

  const handleChange = <K extends keyof Ebike>(key: K, value: Ebike[K]) => {
    setEbike((prev) => ({ ...prev, [key]: value }));
  };

  const resetForm = () => {
    setEbike({
      id: '',
      companyId,
      modelId: '',
      stationId: '',
      serialNumber: '',
      vehicleID: '',
      plateNumber: '',
      odo: 0,
      color: '',
      status: 'Available',
      currentLocation: '',
      lastMaintained: null,
      batteryCapacity: 0,
      range: 0,
      pricePerDay: 0,
      pricePerHour: undefined,
      pricePerWeek: undefined,
      pricePerMonth: undefined,
    });
    setIsUpdateMode(false);
  };

  const handleSave = async () => {
    if (!ebike.modelId || !ebike.serialNumber) {
      alert('Please fill out required fields.');
      return;
    }

    setLoading(true);

    const data = {
      ...ebike,
      companyId,
      odo: Number(ebike.odo),
      batteryCapacity: Number(ebike.batteryCapacity),
      range: Number(ebike.range),
      pricePerDay: Number(ebike.pricePerDay),
      updatedAt: serverTimestamp(),
      createdAt: isUpdateMode ? ebike.createdAt : serverTimestamp(),
      ...(ebike.pricePerHour !== undefined && { pricePerHour: Number(ebike.pricePerHour) }),
      ...(ebike.pricePerWeek !== undefined && { pricePerWeek: Number(ebike.pricePerWeek) }),
      ...(ebike.pricePerMonth !== undefined && { pricePerMonth: Number(ebike.pricePerMonth) }),
    };

    try {
      if (isUpdateMode && ebike.id) {
        await updateDoc(doc(db, 'ebikes', ebike.id), data);
      } else {
        const docRef = await addDoc(collection(db, 'ebikes'), { ...data, id: '' });
        await updateDoc(docRef, { id: docRef.id });
      }

      resetForm();
      onSaveComplete?.();
    } catch (err) {
      console.error('‚ùå Failed to save ebike:', err);
    } finally {
      setLoading(false);
    }
  };

  return {
    ebike,
    setEbike,
    isUpdateMode,
    setIsUpdateMode,
    handleChange,
    handleSave,
    resetForm,
    loading,
  };
}
