'use client';

import { useEffect, useState } from 'react';
import { Ebike, EbikeStatus } from '@/src/lib/ebikes/ebikeTypes';
import {
  collection,
  deleteDoc,
  doc,
  getDocs,
  query,
  where,
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Button } from '@/src/components/ui/button';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

interface Props {
  companyId: string;
  onEdit: (ebike: Ebike) => void;
}

export default function EbikeTable({ companyId, onEdit }: Props) {
  const [ebikes, setEbikes] = useState<Ebike[]>([]);
  const [dialog, setDialog] = useState<{
    open: boolean;
    title: string;
    onConfirm?: () => void;
  }>({ open: false, title: '', onConfirm: undefined });

  const fetchEbikes = async () => {
    try {
      const q = query(collection(db, 'ebikes'), where('companyId', '==', companyId));
      const snapshot = await getDocs(q);
      const data: Ebike[] = snapshot.docs.map((docSnap) => ({
        id: docSnap.id,
        ...(docSnap.data() as Omit<Ebike, 'id'>),
      }));
      setEbikes(data);
    } catch (err) {
      console.error('‚ùå Error fetching ebikes:', err);
    }
  };

  useEffect(() => {
    if (companyId) fetchEbikes();
  }, [companyId]);

  const confirmDelete = (id: string) => {
    setDialog({
      open: true,
      title: 'Delete this Ebike?',
      onConfirm: async () => {
        try {
          await deleteDoc(doc(db, 'ebikes', id));
          await fetchEbikes();
        } catch (err) {
          console.error('‚ùå Failed to delete ebike:', err);
        } finally {
          setDialog({ open: false, title: '', onConfirm: undefined });
        }
      },
    });
  };

  const renderStatusBadge = (status: EbikeStatus) => {
    const colors: Record<EbikeStatus, string> = {
      Available: 'bg-green-100 text-green-800',
      'In Use': 'bg-blue-100 text-blue-800',
      Reserved: 'bg-yellow-100 text-yellow-800',
      'Under Maintenance': 'bg-orange-100 text-orange-800',
      Broken: 'bg-red-100 text-red-800',
    };
    return (
      <span className={`text-xs px-2 py-1 rounded font-semibold ${colors[status]}`}>
        {status}
      </span>
    );
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow overflow-x-auto mt-6">
      <h2 className="text-xl font-semibold mb-4">Ebike List</h2>

      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100 text-gray-700">
          <tr>
            <th className="px-3 py-2 border">Serial</th>
            <th className="px-3 py-2 border">Vehicle ID</th>
            <th className="px-3 py-2 border">Plate</th>
            <th className="px-3 py-2 border">Odo</th>
            <th className="px-3 py-2 border">Color</th>
            <th className="px-3 py-2 border">Location</th>
            <th className="px-3 py-2 border">Status</th>
            <th className="px-3 py-2 border">Battery</th>
            <th className="px-3 py-2 border">Range</th>
            <th className="px-3 py-2 border">$/Hour</th>
            <th className="px-3 py-2 border">$/Day</th>
            <th className="px-3 py-2 border">$/Week</th>
            <th className="px-3 py-2 border">$/Month</th>
            <th className="px-3 py-2 border">Actions</th>
          </tr>
        </thead>
        <tbody>
          {ebikes.map((ebike) => (
            <tr key={ebike.id} className="hover:bg-gray-50">
              <td className="px-3 py-2 border">{ebike.serialNumber}</td>
              <td className="px-3 py-2 border">{ebike.vehicleID}</td>
              <td className="px-3 py-2 border">{ebike.plateNumber}</td>
              <td className="px-3 py-2 border">{ebike.odo} km</td>
              <td className="px-3 py-2 border">{ebike.color}</td>
              <td className="px-3 py-2 border">{ebike.currentLocation}</td>
              <td className="px-3 py-2 border">{renderStatusBadge(ebike.status)}</td>
              <td className="px-3 py-2 border">{ebike.batteryCapacity} Wh</td>
              <td className="px-3 py-2 border">{ebike.range} km</td>
              <td className="px-3 py-2 border">{ebike.pricePerHour ? `$${ebike.pricePerHour}` : '-'}</td>
              <td className="px-3 py-2 border">${ebike.pricePerDay}</td>
              <td className="px-3 py-2 border">{ebike.pricePerWeek ? `$${ebike.pricePerWeek}` : '-'}</td>
              <td className="px-3 py-2 border">{ebike.pricePerMonth ? `$${ebike.pricePerMonth}` : '-'}</td>
              <td className="px-3 py-2 border flex flex-col sm:flex-row gap-1">
                <Button variant="danger" onClick={() => confirmDelete(ebike.id)}>Delete</Button>
                <Button variant="default" onClick={() => onEdit(ebike)}>Edit</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <NotificationDialog
        open={dialog.open}
        title={dialog.title}
        type="confirm"
        onConfirm={dialog.onConfirm}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}
